body 
{
  font-family: sans-serif;
}

.main {
  margin-left: 0%;
}
.image-hover-text-container 
{
  position: relative;
  display: inline-block;
  width: auto;
  height: auto;
  transition: all 0.2s linear;
  margin-top: 10px;
}

.image-hover-image 
{
 display: block;
}
/* Make the image round */
.image-hover-image img
{

  height: 380px;
  margin-right: 20px;
  margin-top: 10px;
  margin-bottom: 10px;

}



.image-hover-text 
{
  position: absolute;
  top: 0;
  width: 100%;  
  height: 100%;
  margin: 0 auto;
  opacity: 0;
  cursor: default;
  transition: opacity 0.2s linear;
}
.image-hover-text:hover 
{
  opacity: 1;
}


.image-hover-text-bubble 
{
  position: relative;
  box-sizing: border-box;
  top: 0; left: 0; right: 100%;
  height: 380px;
  text-align: center;
  background: rgba(218, 211, 211, 0.9);
  border: 1px solid #848484;
  /* margin: 0 auto; */
  margin-right: 20px;
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 25% 8px 5px 8px;
  overflow: hidden;
  font-size: 17px;
  text-align: center;
  word-wrap: break-word;
}

.image-hover-text .image-hover-text-title
{
  font-size: 25px;
  display: block;
}
